@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,400,500,700);
@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);
@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
.text {
  font-family: 'Ubuntu', sans-serif;
  font-size: 1.125rem;
  line-height: 1.5rem;
  font-weight: 200;
  color: #fcfcfc;
  padding: 0.3rem; }

.text-center {
  text-align: center; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.text-justify {
  text-align: justify; }

html {
  font-size: 100%; }

html,
body {
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: 'Ubuntu', 'Roboto', sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 400;
  color: #cccccc; }

a {
  color: inherit;
  text-decoration: none; }

ul {
  list-style: none; }

img {
  max-width: 100%;
  display: block;
  height: auto; }


